import React from 'react';

import '@patternfly/react-core/dist/styles/base.css';
import 'react-responsive-modal/styles.css';
import './stratosphere-notifications.scss';
import useChrome from '@redhat-cloud-services/frontend-components/useChrome';
import StratosphereNotification from './stratosphere-notification';


function ErrorNotification(props) {
  
  const { analytics } = useChrome();

  analytics.track('MarketplaceConnectEvent', {
    outcome:'error',
    partner: props.partnerId,
    errorType: props.type,
    locale: props.local
  });
  
  return (
    <StratosphereNotification
      headerKey={`${props.type}.header`}
      bodyKey={`${props.type}.body`}
      footerKey={`${props.type}.footer`}
      local={props.local}
      {...props} // to inject dependencies
    />
  );
}

export default ErrorNotification;
