import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { withTranslation } from 'react-i18next';

import StratosphereApp from '../src/app/component/stratosphere-app/stratosphere-app';
import ToastNotification from './app/component/shared/toast-notifications/toast-notifications';
import getStratosphereErrorPaths from './app/component/stratosphere-error-paths/get-stratosphere-error-paths';
import Footer from './app/component/shared/footer/footer';

import '@patternfly/react-core/dist/styles/base.css';
import '@patternfly/patternfly/patternfly.css';
import './App.scss';
import ErrorNotification from './app/component/stratosphere-notifications/error-notification';
import PropTypes from 'prop-types';

class App extends React.Component {
  constructor(props) {
    super(props); //this.props.t are default props provide by package i18n
    this.state = {
      newToast: {},
      userDetails: {},
    };
    this.toast = (data) => {
      this.setState({ newToast: data });
    };
    this.setUserDetails = (data) => {
      this.setState({ userDetails: data });
    };
  }

  componentDidMount() {
    insights.chrome.init();
    insights.chrome.identifyApp('connect');

    window.history.pushState(null, null, window.location.href);
    window.onpopstate = function () {
      window.history.go(1);
    };
  }

  componentWillUnmount() {
    const unregister = insights.chrome.on('APP_NAVIGATION', (event) => history.push(`/${event.navId}`));
    return () => {
      unregister();
    };
  }

  render() {
    return (
      <React.Fragment>
        <ToastNotification toastData={this.state.newToast} />
        <Switch>
          <Route exact path="/">
            <ErrorNotification local={this.props.t} type="default-error" />
          </Route>
          <Route
            path="/:partnerId"
            // must render this way so the `partnerId` match can be injected via props
            render={(props) => (
              <StratosphereApp
                local={this.props.t}
                i18n={this.props.i18n}
                useToast={this.toast}
                setUserDetails={this.setUserDetails}
                getErrorPaths={getStratosphereErrorPaths}
                {...props}
              />
            )}
          />
        </Switch>
        <Footer local={this.props.t} />
      </React.Fragment>
    );
  }

  static get propTypes() {
    return {
      i18n: PropTypes.any,
      t: PropTypes.func,
    };
  }
}

export const AppWithTranslation = withTranslation()(App);
